<template>
    <div class="indexNews">
        <p>
            <span></span>
            <a>>></a>
        </p>
        <ul>
            <li v-for="item in newsList" :key="item.id">
                <span>{{textLenght(item.title,56,true)}}</span>
                <span>{{item.creatime}}</span>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  data() {
    return {
      newsList: [
        {
          title: "4",
          creatime: "2017-12-10",
          id: "1"
        },
        {
          title: "44",
          creatime: "2017-12-10",
          id: "2"
        },
        {
          title: "44",
          creatime: "2017-12-10",
          id: "3"
        },
        {
          title: "",
          creatime: "2017-12-10",
          id: "4"
        },
        {
          title: "4",
          creatime: "2017-12-10",
          id: "5"
        }
      ]
    };
  },
  methods: {
    textLenght:function(str, len, hasDot) {
            var newLength = 0
            var newStr = ""
            var chineseRegex = /[^\x00-\xff]/g
            var singleChar = ""
            var strLength = str.replace(chineseRegex, "**").length
            for (var i = 0; i < strLength; i++) {
                singleChar = str.charAt(i).toString()
                if (singleChar.match(chineseRegex) != null) {
                    newLength += 2
                } else {
                    newLength++
                }
                if (newLength > len) {
                    break
                }
                newStr += singleChar
            }

            if (hasDot && strLength > len) {
                newStr += "..."
            }
            return newStr
        }
  }
};
</script>
<style>
.indexNews {
  float: right;
  width: 590px;
  height: 304px;
  border: 1px solid #efefef;
}
.indexNews p {
  display: flex;
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #efefef;
  justify-content: space-between;
  align-items: center;
  background: #fcfcfc;
}
.indexNews p span {
  padding-left: 20px;
  font-size: 14px;
  font-weight: bold;
  color: #4a4a4a;
}
.indexNews a {
  padding-right: 20px;
  color: #cccccc;
  cursor: pointer;
}
.indexNews ul {
  width: 530px;
  margin: auto;
  margin-top: 10px;
  list-style: none;
  counter-reset: count;
}
.indexNews ul li {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 48px;
  border-bottom: 1px dashed #e5e7ed;
}
.indexNews ul li:last-child {
  border: 0;
}
.indexNews ul li:before {
  content: " ";
  color: #b3b3b3;
}
.indexNews li span:nth-child(1) {
  font-size: 14px;
  color: #565656;
  padding: 0 10px;
}
.indexNews li span:nth-child(2) {
  font-size: 14px;
  color: #b3b3b3;
}
</style>


