<template>
    <div class="newBodys">
        <ul>
            <li v-for="(item ,index) in data" :key="index" :class="{active:num == index}" @click="num = index">{{item.type}}</li>
        </ul>
        <section class="newBodyItem" v-for="(item ,index) in data[num].datas" :key="index">
            <img :src="item.img" alt="">
            <div>
                <p>{{textLenght(item.name,20,true)}}</p>
                <p>
                    <span v-for="item in item.label" :key="item">{{item}}</span>
                </p>
            </div>
        </section>
    </div>
</template>
<script>
export default {
  data() {
    return {
      num: "0",
      data: [
        {
          type: "",
          datas: [
            {
              name: "1",
              img: "http://head.ppsj.com.cn/0/868.gif",
              label: ["", ""],
              id: "1"
            },
            {
              name: "",
              img: "http://head.ppsj.com.cn/0/870.gif",
              label: ["", ""],
              id: "2"
            },
            {
              name: "JAVA",
              img: "http://head.ppsj.com.cn/0/297.gif",
              label: ["", ""],
              id: "3"
            },
            {
              name: "",
              img: "http://head.ppsj.com.cn/2/2145.gif",
              label: ["", ""],
              id: "4"
            }
          ]
        },
        {
          type: "",
          datas: [
            {
              name: "teak",
              img: "http://head.ppsj.com.cn/0/868.gif",
              label: ["", ""],
              id: "1"
            },
            {
              name: "",
              img: "http://head.ppsj.com.cn/0/870.gif",
              label: ["", ""],
              id: "2"
            },
            {
              name: "JAVA",
              img: "http://head.ppsj.com.cn/0/297.gif",
              label: ["", ""],
              id: "3"
            },
            {
              name: "",
              img: "http://head.ppsj.com.cn/2/2145.gif",
              label: ["", ""],
              id: "4"
            }
          ]
        },
        {
          type: "",
          datas: [
            {
              name: "specialized",
              img: "http://head.ppsj.com.cn/0/868.gif",
              label: ["", ""],
              id: "1"
            },
            {
              name: "",
              img: "http://head.ppsj.com.cn/0/870.gif",
              label: ["", ""],
              id: "2"
            },
            {
              name: "JAVA",
              img: "http://head.ppsj.com.cn/0/297.gif",
              label: ["", ""],
              id: "3"
            },
            {
              name: "",
              img: "http://head.ppsj.com.cn/2/2145.gif",
              label: ["", ""],
              id: "4"
            }
          ]
        }
      ]
    };
  },
  methods: {
    textLenght: function(str, len, hasDot) {
      var newLength = 0;
      var newStr = "";
      var chineseRegex = /[^\x00-\xff]/g;
      var singleChar = "";
      var strLength = str.replace(chineseRegex, "**").length;
      for (var i = 0; i < strLength; i++) {
        singleChar = str.charAt(i).toString();
        if (singleChar.match(chineseRegex) != null) {
          newLength += 2;
        } else {
          newLength++;
        }
        if (newLength > len) {
          break;
        }
        newStr += singleChar;
      }

      if (hasDot && strLength > len) {
        newStr += "...";
      }
      return newStr;
    }
  }
};
</script>
<style>
.newBodys {
  float: left;
  width: 590px;
  height: 304px;
  border: 1px solid #efefef;
  border-top: 0;
}
.newBodys ul {
  display: flex;
  width: 100%;
  height: 40px;
  justify-content: center;
  align-items: center;
}
.newBodys ul li {
  display: flex;
  flex-grow: 1;
  height: 40px;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: bold;
  background: #fcfcfc;
  border: 1px solid #efefef;
  border-left: 0;
  color: #4a4a4a;
}
.newBodys ul li:nth-child(3) {
  border-right: 0;
}
.newBodys ul li.active {
  height: 38px;
  border-top: 3px solid #337ffb;
  border-bottom: 0;
  color: #337ffb;
  background: #ffffff;
}
.newBodyItem {
  float: left;
  display: flex;
  width: 270px;
  height: 100px;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #efefef;
  margin-left: 20px;
  margin-top: 20px;
}
.newBodyItem:nth-child(2n + 1) {
  margin-left: 10px;
}
.newBodyItem img {
  width: 70px;
  height: 70px;
  margin: 15px;
  border-radius: 100%;
}
.newBodyItem p:nth-child(1) {
  font-size: 14px;
  color: #0b0b0b;
  line-height: 30px;
}
.newBodyItem p:nth-child(2) span {
  padding: 0 5px;
  line-height: 24px;
  font-size: 12px;
  color: #ccc;
  border: 1px solid #e1e1e1;
  margin-right: 5px;
}
</style>



