<template>
	<div class="bodytabs" v-if="data" :style="data.BorderColor">
		<div class="bodyTabSidebar" :style="data.BgColor">
			<p>{{data.name}}</p>
			<span class="siderLine"></span>
			<span class="siderDes">{{data.tabsdes}}</span>
			<ul>
				<li v-for='(itme,index) in data.child' :key="index" v-if="index < 5" :class='{active:num === itme.id}' @click="itemList(itme.id)" :style="data.tabBgColor">
					<span v-if="num === itme.id" :style="data.activeFontColor">{{itme.name}}</span>
					<span v-else>{{itme.name}}</span>
				</li>
			</ul>
		</div>
		<div class="bodyTabCentent">
			<section v-for="(item,index) in items" :key="index">
				<img :src="path+item.cover_img" alt="">
				<div>
					<p>{{item.name}}</p>
					<p>
						<span v-for="label in item.label"  :key="label">{{label}}</span>
					</p>
					<p>{{item.service}}:<span>{{item.pay}}</span><i></i></p>
				</div>
			</section>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {
    path: path,
    num: '',
    itemListPath:path+'/index/index/index-company',
    items:''
	}
  },
  props: ["data"],
  mounted() {
    
  },
  watch: {
    data(val) {
      this.data = val
      this.num = this.data.child[0].id
      this.itemList(this.num)
    }
  },
  methods:{
    itemList: function(id) {
      this.num = id
      var _this = this;
      _this.num = id;
      this.$http
        .get(_this.itemListPath + "?qualification_id=" + id, {
          emulateJSON: true
        })
        .then(
          function(response) {
            _this.items = response.data;
          },
          function(response) {
            alert("");
          }
        );
    }
  }
};
</script>

<style media="screen">
.bodytabs {
  width: 100%;
  height: 398px;
  border-top: 3px solid;
  background: #ffffff;
  margin-top: 20px;
}
.bodyTabSidebar {
  float: left;
  width: 208px;
  height: 368px;
  padding-top: 30px;
  padding-left: 20px;
  color: #ffffff;
}
.bodyTabSidebar p {
  font-size: 22px;
  font-weight: bold;
}
.bodyTabSidebar .siderLine {
  display: block;
  width: 88px;
  height: 0px;
  border-top: 3px solid #ffffff;
  margin-top: 5px;
  margin-bottom: 15px;
}
.bodyTabSidebar .siderDes {
  font-size: 14px;
}
.bodyTabSidebar ul {
  float: right;
  margin-top: 50px;
}
.bodyTabSidebar ul li {
  height: 38px;
  line-height: 38px;
  width: 140px;
  text-align: right;
  padding-right: 20px;
  margin-bottom: 10px;
}
.bodyTabSidebar ul li.active {
  background: #ffffff !important;
}
.bodyTabCentent {
  float: left;
  width: 982px;
}
.bodyTabCentent section {
  float: left;
  display: flex;
  width: 490px;
  height: 132px;
  border-bottom: 1px solid #f0f0ff;
  justify-content: left;
  align-items: center;
}
.bodyTabCentent section:nth-child(2n + 1) {
  border-right: 1px solid #f0f0f0;
}
.bodyTabCentent section img {
  width: 88px;
  height: 88px;
  border-radius: 100%;
  margin-left: 30px;
  margin-right: 20px;
}
.bodyTabCentent section div p:nth-child(1) {
  font-size: 16px;
  color: #0b0b0b;
  line-height: 30px;
}
.bodyTabCentent section div p:nth-child(2) span {
  padding: 0 10px;
  line-height: 24px;
  font-size: 12px;
  color: #ccc;
  border: 1px solid #e1e1e1;
  margin-right: 5px;
}
.bodyTabCentent section div p:nth-child(3) {
  line-height: 24px;
  font-size: 12px;
  color: #b3b3b3;
}
.bodyTabCentent section div p:nth-child(3) span {
  color: #ffc373;
  font-size: 14px;
}
.bodyTabCentent section div p:nth-child(3) i {
  color: #898989;
  font-size: 14px;
}
</style>



