<template>
  <div>
    <div class="company_selet_box">
      <section>
        <p></p>
        <span v-for="(item,index) in typeTree" @click="toggleTabs_type(index)" :class="{active:index==typevalue}">{{item.name}}</span>
      </section>
      <section>
        <p></p>
        <span v-for="(item,index) in content" @click="toggleTabs_content(index)" :class="{active:index==contentvalue}">{{item.name}}</span>
      </section>
      <section>
        <p></p>
        <span v-for="(item,index) in address" @click="toggleTabs_address(index)" :class="{active:index==addressvalue}">{{item.name}}</span>
      </section>
    </div>
    <div class="company_list_box common_type_box">
      <p class="company_list_head common_type_head">
          <span>{{total}}</span>
      </p>
      <organizationList ref="list" v-on:page-change="dealChangePage"></organizationList>
      <div class="list-pagination-wrap">
          <ul class="list-pagination">
            <li @click="dealClickPage('pre')"></li>
            <!-- <li class="active">1</li>
            <li>2</li>
            <li>3</li> -->
            <li v-for="(item,index) in page_total" @click="dealClickPage(item)" v-bind:class="{active:item==page}">{{item}}</li>
            <li @click="dealClickPage('next')"></li>
          </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import organizationList from '../organizationList/organizationList'

  export default {
    data(){
      return{
        typevalue:'0',
        contentvalue:'0',
        addressvalue:'0',

        typeTree:[],

        //
        page:1,
        page_total:2,
        total:15,

        type:[
          {
            name:'',
            value:'1'
          },
          {
            name:'',
            value:'2'
          },
          {
            name:'',
            value:'3'
          },
          {
            name:'',
            value:'4'
          },
        ],
        content:[
          {
            name:'',
            value:'1'
          },
          {
            name:'',
            value:'2'
          },
          {
            name:'',
            value:'3'
          },
          {
            name:'',
            value:'4'
          },
          {
            name:'',
            value:'5'
          },
        ],
        address:[
          {
            name:'',
            value:'1'
          },
          {
            name:'',
            value:'2'
          },
          {
            name:'',
            value:'3'
          },
          {
            name:'',
            value:'4'
          },
          {
            name:'',
            value:'5'
          },
          {
            name:'',
            value:'6'
          },
          {
            name:'',
            value:'7'
          },
          {
            name:'',
            value:'8'
          }
        ]
      }
    },
    created(){


      //+
      this.getServiceType();

      //
      this.getLocationList();

  
      //



    },
    methods:{
        dealChangePage:function(total_page,total){
            //alert(total);
            this.page_total = total_page;
            this.total = total;
        },

        dealClickPage:function(index){

          if(index  == 'pre'){
            this.page--;
            if(this.page < 1){
              this.page = 1;
            }
          }
          else if(index == 'next'){
            this.page++;
            if(this.page>this.page_total){
              this.page = this.page_total;
            }
          }
          else{
            this.page = index;
          }
          

          //OK
          //alert(this.page)


          var pid = this.type[this.typevalue].id;
          var id = this.content[this.contentvalue].id;
          var area_id = this.address[this.addressvalue].id;

          this.$refs.list.changeType(pid,id,area_id,this.page);


        },
        getServiceType:function(){

          var url = path + '/index/company/company-type';
          this.$http.get(url).then(function(r){

            var td = r.data;
 
            this.typeTree = td;

            this.type = td;
            this.content = this.type[0].child;
            

          })


        },
        getLocationList:function(){
          var url = path + '/index/address/child-address?address_id=1';
          this.$http.get(url).then(function(r){

            var td = r.data;
          
            this.address = td;

          })
        },

        toggleTabs_type:function(value){
            this.typevalue=value;

            //
            this.contentvalue = 0;
            this.content = this.typeTree[value].child;


            //
            var pid = this.type[this.typevalue].id;
            //: OK
            //alert(JSON.stringify(this.type[this.typevalue]));
            //alert(pid);
            var id = this.content[this.contentvalue].id;
            var area_id = this.address[this.addressvalue].id;


            this.$refs.list.changeType(pid,id,area_id,this.page);

        },
        toggleTabs_content:function(value){
            this.contentvalue=value;

            var pid = this.type[this.typevalue].id;
            var id = this.content[this.contentvalue].id;
            var area_id = this.address[this.addressvalue].id;

            this.$refs.list.changeType(pid,id,area_id,this.page);

        },
        toggleTabs_address:function(value){
            this.addressvalue=value;

            var pid = this.type[this.typevalue].id;
            var id = this.content[this.contentvalue].id;
            var area_id = this.address[this.addressvalue].id;


            this.$refs.list.changeType(pid,id,area_id,this.page);
        }
    },
    watch:{
      typevalue:function(){
        console.log(this.typevalue);
      }
    },
    components:{
      organizationList
    }
  }
</script>

<style media="screen">
  .list-pagination-wrap{
    padding-top: 15px;
    padding-bottom: 15px;
    text-align: center;
  }
  .list-pagination{
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fbfbfb;
    font-size:0em;
  }
  .list-pagination li{
    

    display: inline-block;
    min-width: 32px;
    height: 30px;
    text-align: center;
    line-height: 28px;
    padding: 2px;
    /*border-left: 1px solid #d00;*/
    box-sizing: border-box;
    border-right: 1px solid #ccc;

    font-size:11px;
    color: #aaa;

  }
  .list-pagination li:last-child{
    border-right: none;
  }

  .list-pagination li.active{
    background-color: #559CF1;
    color: white;
  }

</style>



