<template>
  <div class="bannerBox">
	  <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide"><img src="https://hyy1115.github.io/blog/files/banner3.8df7bd4.jpg" alt=""></div>
          <div class="swiper-slide"><img src="https://hyy1115.github.io/blog/files/banner1.05867d8.png" alt=""></div>
        </div>
        <div class="swiper-pagination"></div>
    </div>
    <indexLogin></indexLogin>
  </div>
</template>
<script>
// import Swiper from "swiper";
import indexLogin from '../indexLogin/indexLogin'
export default {
  mounted() {
    var swiper = new Swiper('.swiper-container', {
      pagination: {
        el: '.swiper-pagination',
      },
      loop : true,
      autoplay: true
    });
  },
  components:{
    indexLogin
  }
};
</script>
<style>
.bannerBox{
  position: absolute;
  width: 100%;
  left: 0;
  min-width: 1210px;
}
.swiper-container {
  width: 100%;
  height: 408px;
}
.swiper-wrapper {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.swiper-slide img {
  width: 100%;
  height: 100%;
}

</style>



