<template>
  <div >
    apiTest

    <div>
      <input class="url-box" type="text" v-model="url" />
      <button @click="dealGet">GET</button>
    </div>

    <div>
      <button @click="system1"></button>
      <button @click="system2"></button>
    </div>

    <div>
      <button @click="help1"></button>
      <button @click="help2"></button>
      <button @click="help3"></button>
    </div>

    <div>
      <button @click="index1"></button>
      <button @click="index2"></button>
      <button @click="index3"></button>
      <button @click="index4"></button>
    </div>
    

    <div>
      <pre v-html="response"></pre>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">

  import request from '../../ajax/request.js'

  import Vue from 'vue'

  export default {

    data:function(){

      return {
        response:"",
        url:""
      }

    },

    methods:{

      formatHtml:function(obj){
        var objStr = JSON.stringify(obj,null,4);
        var html = objStr.replace(/\n/g, '<br>').replace(/\s/g, ' '); 
        return html;
      },

      dealGet:function(){

        if(this.url.indexOf('http://')!=0){
          this.url = 'http://119.23.229.133'+this.url
        }

        var self = this;
        Vue.http.get(this.url).then(function(r){

          self.response = self.formatHtml(r.data);

        })

      },

      system1:function(){
        var self = this;
        request.friendly({},function(data){

          
          self.response = self.formatHtml(data);

        })

      },

      system2:function(){
        var self = this;
        request.systemSetting({},function(data){

          
          self.response = self.formatHtml(data);

        })

      },

      help1:function(){
        var self = this;
        request.helpCategory({},function(data){

          
          self.response = self.formatHtml(data);

        })

      },
      help2:function(){
        var self = this;
        request.helpList({'category_id':1},function(data){

          
          self.response = self.formatHtml(data);

        })

      },
      help3:function(){
        var self = this;
        request.helpDetail({'help_id':11},function(data){

          
          self.response = self.formatHtml(data);

        })

      },

      index1:function(){
        var self = this;
        request.helpDetail({'help_id':11},function(data){
          self.response = self.formatHtml(data);
        })
      },

      index2:function(){
        var self = this;
        request.helpDetail({'help_id':11},function(data){

          self.response = self.formatHtml(data);

        })
      },

      index3:function(){
        var self = this;
        request.helpDetail({'help_id':11},function(data){

          self.response = self.formatHtml(data);

        })
      },

      index4:function(){
        var self = this;
        request.helpDetail({'help_id':11},function(data){

          self.response = self.formatHtml(data);

        })
      }


    }

  }
</script>

<style media="screen">
  .url-box{
    width: 50%;
  }
</style>



