<template>
  <div>
    <div class="company_selet_box">
      <section>
        <p></p>
        <span v-for="item in state" @click="toggleTabs_state(item.value)" :class="{active:item.value==statevalue}">{{item.name}}</span>
      </section>
      <section>
        <p></p>
        <span v-for="item in capital" @click="toggleTabs_capital(item.value)" :class="{active:item.value==capitalvalue}">{{item.name}}</span>
      </section>
      <section>
        <p></p>
        <span v-for="item in address" @click="toggleTabs_address(item.value)" :class="{active:item.value==addressvalue}">{{item.name}}</span>
      </section>
      <section>
        <p></p>
        <span v-for="item in city" @click="toggleTabs_city(item.value)" :class="{active:item.value==cityvalue}">{{item.name}}</span>
      </section>
      <section>
        <p></p>
        <span v-for="item in high" @click="toggleTabs_high(item.value)" :class="{active:item.value==highvalue}">{{item.name}}</span>
      </section>
    </div>
    <div class="company_list_box common_type_box">
      <p class="company_list_head common_type_head">
          <span>{{total}}</span>
      </p>
      <firmList ref="companyList"></firmList>
      <div class="list-pagination-wrap">
          <ul class="list-pagination">
            <li @click="dealClickPage('prev')"></li>
            <!-- <li class="active">1</li>
            <li>2</li>
            <li>3</li> -->
            <li v-for="(item,index) in page_total" @click="dealClickPage(item)" v-bind:class="{active:item==page}">{{item}}</li>
            <li @click="dealClickPage('next')"></li>
          </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import firmList from '../firmList/firmList'

  import request from "../../ajax/request.js";

  export default {


    created(){

      //
      this.downloadData();

    },

    data(){
      return{

        //
        page_total:2,
        page:1,

        total:0,


        statevalue:'all',
        capitalvalue:'1',
        addressvalue:'1',
        cityvalue:'1',
        highvalue:'1',

        state:[
          {
            name:'',
            value:'all'
          },
          {
            name:'',
            value:'1'
          },
          {
            name:'',
            value:'2'
          },
          {
            name:'',
            value:'3'
          },
          {
            name:'',
            value:'4'
          }
        ],
        capital:[
          {
            name:'',
            value:'1'
          },
          {
            name:'500',
            value:'2'
          },
          {
            name:'500~1000',
            value:'3'
          },
          {
            name:'1000~5000',
            value:'4'
          },
          {
            name:'5000',
            value:'5'
          }
        ],
        address:[
          {
            name:'',
            value:'1'
          },
          {
            name:'',
            value:'2'
          },
          {
            name:'',
            value:'3'
          },
          {
            name:'',
            value:'4'
          },
          {
            name:'',
            value:'5'
          }
        ],
        city:[
          {
            name:'',
            value:'1'
          },
          {
            name:'',
            value:'2'
          }
        ],
        high:[
          {
            name:'',
            value:'1'
          },
          {
            name:'',
            value:'2'
          },
          {
            name:'',
            value:'3'
          },
          {
            name:'',
            value:'4'
          }
        ]
      }
    },
    methods:{

      downloadData:function(){

        var dict = {}
        if(this.page>0){
          dict['page'] = this.page
        }
        //,
        if(this.statevalue != "all"){
          dict['manage_status'] = this.statevalue;
        }

        var self = this;
        request.companyList(dict,function(data){

          console.log(data);

          //self.firmList = data.data;

          self.total = data.total;

          //, 
          var companyListComponent = self.$refs.companyList;
          companyListComponent.showList(data.data);

          //total
          var total = data.total;
          var page_total = Math.ceil(data.total/data.per_page);
          self.page_total = page_total;
          console.log("page_total = "+page_total)

        })
      },

      dealClickPage:function(index){

        if(index == this.page){
          return;
        }

        var newPage = this.page;
        //alert(index)
        if(index=="prev"){
          newPage--;
          if(newPage<1){
            newPage = 1;
          }
        }else if(index=="next"){
          newPage++;
          if(newPage>this.page_total){
            newPage = 1;
          }
        }else{
          newPage = index;
        }

        this.page = newPage;
        //alert(newPage)

        this.downloadData();

      },

      //
      toggleTabs_state:function(value){
          this.statevalue=value;
          this.downloadData();
      },
      toggleTabs_capital:function(value){
          this.capitalvalue=value;
      },
      toggleTabs_address:function(value){
          this.addressvalue=value;
      },
      toggleTabs_city:function(value){
          this.cityvalue=value;
      },
      toggleTabs_high:function(value){
          this.highvalue=value;
      }
    },

    components:{
      firmList
    }
  }
</script>

<style media="screen">

.list-pagination-wrap{
    padding-top: 15px;
    padding-bottom: 15px;
    text-align: center;
  }
  .list-pagination{
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fbfbfb;
    font-size:0em;
  }
  .list-pagination li{
    

    display: inline-block;
    min-width: 32px;
    height: 30px;
    text-align: center;
    line-height: 28px;
    padding: 2px;
    /*border-left: 1px solid #d00;*/
    box-sizing: border-box;
    border-right: 1px solid #ccc;

    font-size:11px;
    color: #aaa;

  }
  .list-pagination li:last-child{
    border-right: none;
  }

  .list-pagination li.active{
    background-color: #559CF1;
    color: white;
  }

</style>



