<template>
  <div>
    <ul class="firm_list">
      <li v-for="item in list">
        <img :src="path+item.cover_img">
        <section>
          <router-link :to="{name:'organizationDetails',params:{id:item.id}}">{{item.name}}</router-link>
          <div class="firm_list_tabs">
            <span></span>
            <span></span>
            <span></span>
          </div>

          <span>{{item.address}} </span>
        </section>
        <p>{{item.city?item.city.name:''}}</p>
      </li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        path:path,

        //
        qualification_pid:1,
        qualification_id:2,
        area_id:2,
        page:1,

        /*
        qualification_pid:2,
        qualification_id:1,
        area_id:2,
        */

        //
        list:[],

        firmList:[
          {
            name: '111',
            legal: '',
            tel: '17712422870',
            address: '3402',
            id: '1',
            brand: '11',
            patent: '10',
            copyright: '9',
            logoicon: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1498026496543&di=eaf157cc0a672d101466af20163ca718&imgtype=0&src=http%3A%2F%2Fimage.knewone.com%2Fbrands%2Fb3a9bc05baa73ca29255b55e6bcf0c82.jpg'
          },
          {
            name: '2',
            legal: '',
            tel: '17712422870',
            address: '3402',
            id: '2',
            brand: '11',
            patent: '10',
            copyright: '9',
            logoicon: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1498026496543&di=eaf157cc0a672d101466af20163ca718&imgtype=0&src=http%3A%2F%2Fimage.knewone.com%2Fbrands%2Fb3a9bc05baa73ca29255b55e6bcf0c82.jpg'
          },
          {
            name: '3',
            legal: '',
            tel: '17712422870',
            address: '3402',
            id: '3',
            brand: '11',
            patent: '10',
            copyright: '9',
            logoicon: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1498026496543&di=eaf157cc0a672d101466af20163ca718&imgtype=0&src=http%3A%2F%2Fimage.knewone.com%2Fbrands%2Fb3a9bc05baa73ca29255b55e6bcf0c82.jpg'
          },
          {
            name: '4',
            legal: '',
            tel: '17712422870',
            address: '3402',
            id: '4',
            brand: '11',
            patent: '10',
            copyright: '9',
            logoicon: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1498026496543&di=eaf157cc0a672d101466af20163ca718&imgtype=0&src=http%3A%2F%2Fimage.knewone.com%2Fbrands%2Fb3a9bc05baa73ca29255b55e6bcf0c82.jpg'
          },
          {
            name: '5',
            legal: '',
            tel: '17712422870',
            address: '3402',
            id: '5',
            brand: '11',
            patent: '10',
            copyright: '9',
            logoicon: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1498026496543&di=eaf157cc0a672d101466af20163ca718&imgtype=0&src=http%3A%2F%2Fimage.knewone.com%2Fbrands%2Fb3a9bc05baa73ca29255b55e6bcf0c82.jpg'
          }
        ]
      }
    
    },
    created(){
      //alert("hh")

      this.getList();

    },
    methods:{
      changeType:function(pid,id,location,page){

        //: 
        console.log("pid="+pid+" id="+id+" loc="+location+" page="+page);

        this.page = page;
        this.qualification_pid = pid;
        this.qualification_id = id;
        this.area_id = location;
        this.getList();
      },

      getList: function() {


        //url
        var url = path+"/index/company?page="+this.page+"&qualification_pid="+this.qualification_pid+"&qualification_id="+this.qualification_id+"&area_id="+this.area_id;

        //
        this.$http.get(url).then((response) => {
          //alert("down")

          var td = response.data;
          this.list = td.data;
          //console.log(JSON.stringify(this.list));

          //, , 
          var page_total = td.last_page;
          var total = td.total;
          this.$emit("page-change",page_total,total);

        }).catch(function(response) {
          console.log(response)
        })
      }
    }


  }
</script>

<style media="screen">
  .firm_list li{
    display: flex;
    height: 170px;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #e5e7ed;
  }
  .firm_list li img{
    width: 110px;
    height: 110px;
    border: 1px solid #e5e7ed;
    margin: 0 20px;
  }
  .firm_list li section{
    flex-grow: 1;
  }
  .firm_list li section a{
    display: block;
    font-size: 16px;
    color: #616161;
    line-height: 32px;
  }
  .firm_list li section span{
    display: block;
    font-size: 14px;
    color: #b3b3b3;
    line-height: 25px;
  }
  .firm_list li p{
    width: 105px;
    font-size: 14px;
    color: #b3b3b3;
    text-align: center;
  }
  .firm_list_tabs{
    display: flex;
    height: 50px;
    justify-content: flex-start;
    align-items: center;
  }
  .firm_list_tabs span{
    padding: 0px 15px;
    border: 1px solid #e5e7ed;
    margin-right: 10px;
  }
</style>



